import listCell from '@/components/mix-list-cell';
import sPopup from '@/components/s-popup';
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		listCell,
		sPopup,
	},
	data() {
		return {
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			user: {},
			visible: false,
		}
	},
	onLoad() {
		this.checkToken();
	},
	onShow() {
		this.getuser();
	},




















	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		getuser(){
			this.$api.auth.identify().then(data => {
				console.log(data)
				this.user = data;
			})
		},
		getImg() {
			uni.getImageInfo({
				src: '../../static/temp/index/er.png',
				success: function(image) {
					console.log('图片信息：', JSON.stringify(image));
					uni.saveImageToPhotosAlbum({
						filePath: image.path,
						success: function() {
							console.log('save success');
							uni.showToast({
								title: '图片保存成功',
								icon: 'none',
								duration: 2200
							});
						}
					});
				}
			});
		},
		relation() {
			this.visible = true;
		},
		checkToken() {
			uni.getSetting({
				success(res) {
					console.log("授权：", res);
					if (!res.authSetting['scope.userInfo']) {
						//这里调用授权
						console.log("当前未授权");
					} else {
						//用户已经授权过了
						console.log("当前已授权");
					}
				}
			})
			// let token = uni.getStorageSync('auth_token');
			// if (token) {
			// 	uni.switchTab({
			// 		url: '../index/index'
			// 	})
			// }
		},
		appLoginWx() {
			var that = this;
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: (res) => {
								// _self.authorization = res.code;
								uni.getUserInfo({
									provider: 'weixin',
									success: (info) => { //这里请求接口
										console.log(info);
										let code = res.code;
										let nickname = info.userInfo.nickName
										let avatar = info.userInfo.avatarUrl
										that.$api.auth.login(code, avatar, nickname);
										uni.switchTab({
											url: '../index/index'
										})
									},
									fail: () => {
										uni.showToast({
											title: "微信登录授权失败",
											icon: "none"
										});
									}
								})

							},
							fail: () => {
								uni.showToast({
									title: "微信登录授权失败",
									icon: "none",
								});
							}
						})

					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: "none"
						});
					}
				}
			});
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navToRecharge(user_id) {
			console.log(user_id);
			let id = user_id;
			uni.navigateTo({
				url: `/pages/user/recharge?id=${id}`
			})
		},
		navToBalance(user_id) {
			console.log(user_id);
			let id = user_id;
			uni.navigateTo({
				url: `/pages/user/balance?id=${id}`
			})
		},
		navTo(url) {
			if (!this.hasLogin) {
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	}
}
